.OptionsSection .content{display:flex;flex-direction:column;gap:10px;width:100%}.OptionsSection .content a:hover{text-decoration:underline}.OptionsSection .content h4{color:#999;font-weight:bold;padding-bottom:1em;border-bottom:1px solid hsla(0,0%,80%,.5);margin-bottom:1em;line-height:1.6}.OptionsSection .content .Info{color:#999;letter-spacing:.5px;font-style:italic;font-weight:bold;padding-top:1em;border-top:1px solid hsla(0,0%,80%,.5);margin-top:1em}.OptionsSection .content .Fade{opacity:.5}.OptionsSection .content>ul.Items{list-style-type:none;margin:0;padding:0;display:flex;flex-direction:column;gap:10px}.OptionsSection .content>ul.Items>li{display:flex;flex-direction:column;align-items:center;padding:0;border:1px solid hsla(0,0%,80%,.5);border-radius:10px;overflow:hidden;transition:all var(--transitionTimeMs)}.OptionsSection .content>ul.Items>li:hover{border-color:#ccc}.OptionsSection .content>ul.Items>li button{width:100%;padding:20px;padding-bottom:12px;display:flex;flex-direction:row;align-items:center;font-size:1rem;cursor:pointer;background-color:rgba(0,0,0,0)}.OptionsSection .content>ul.Items>li button,.OptionsSection .content>ul.Items>li button .Text{transition:all var(--transitionTimeMs)}.OptionsSection .content>ul.Items>li button .Icon{display:inline-block;width:120px;height:52px;background-repeat:no-repeat;background-position:center;min-width:120px}.OptionsSection .content>ul.Items>li button .Text{display:inline-block;font-weight:bold}.OptionsSection .content>ul.Items>li button:hover{background-color:hsla(0,0%,80%,.3)}.OptionsSection .content>ul.Items>li button[aria-expanded=true]{background-color:hsla(0,0%,80%,.25)}.OptionsSection .content>ul.Items>li button[aria-expanded=true]:hover{background-color:hsla(0,0%,80%,.35)}.OptionsSection .content>ul.Items>li#Selfcare .Icon{background-image:url("../images/Options/Selfcare.svg")}.OptionsSection .content>ul.Items>li#Provider .Icon{background-image:url("../images/Options/Provider.svg")}.OptionsSection .content>ul.Items>li#Cdek .Icon{background-image:url("../images/Options/Cdek.svg")}.OptionsSection .content>ul.Items>li#Dl .Icon{background-image:url("../images/Options/Dl.svg")}.OptionsSection .content>ul.Items>li#Yandex .Icon{background-image:url("../images/Options/Yandex.svg")}.OptionsSection .content>ul.Items>li .collapse{padding:20px;padding-bottom:10px}.OptionsSection .content>ul.Items>li .collapse>:first-child{margin-top:0}.OptionsSection .content>ul.Items>li .collapse:not(.show){display:none}.OptionsSection .content>ul.Items>li .collapse.show{background-color:hsla(0,0%,80%,.1)}